div>.btnRegisterOne{
    border-radius: 30px;
    padding: 10px 20px 10px 50px;
    font-size: 1.1em;
}

.imgCarouselOne{
    object-fit: cover;
    height: 570px;
    filter: brightness(.6);
}

.carousel-caption>h1{
    font-size: 50px;
    font-weight: bold;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}


.carouselCaptionOne>h1, .carouselCaptionOne>p, .carouselCaptionOne>button{
    position: relative;
    left: -150px;
    top: -140px;
    text-align: center;
}
.carouselCaptionOne>p{
    color:  rgb(208, 204, 218);
    font-size: 20px;
}

.playIcon{
    position: absolute;
    top: 7px;
    left: 12px;
    width: 20%;
    filter: invert(100%);
}

.principalCarousel>.carousel-control-next>.carousel-control-next-icon, .principalCarousel>.carousel-control-prev>.carousel-control-prev-icon{
    display: none !important;
}

.principalCarousel>.carousel-indicators>li{
    width: 10px !important;
    height: 10px !important;
    border-radius: 100% !important;
    background-color: rgb(173, 162, 221) !important;
}

